import { useTranslation } from "react-i18next";

export function Footer(props) {
   const { t } = useTranslation();

   return (
      <footer className="footer">
          <div className="footer__container container">
              <img src="images/logo.svg" alt="logo" className="footer__logo"/>
              <ul className="footer__links">
                  <li><a href="#" className="footer__link"><img src="images/instagram.svg" alt="instagram" /></a></li>
                  <li><a href="#" className="footer__link"><img src="images/twitter.svg" alt="twitter" /></a></li>
                  <li><a href="#" className="footer__link"><img src="images/github.svg" alt="github" /></a></li>
                  <li><a href="#" className="footer__link"><img src="images/telegram.svg" alt="telegram" /></a></li>   
              </ul>
          </div>
      </footer>
   );
}