import { Header } from './Header';
// import { Link } from 'react-router-dom';
import { useTranslation } from "react-i18next";

export function TopBlock(props) {
   const { t } = useTranslation();
    
    return (
      <section className="hero">
        <Header />
        <div className="container hero__container">
            <div className="hero__wrapper">
                <img src="images/hero-img.png" alt="telephone" className='hero__img'/>
                <img src="images/hero-img-mobile.png" alt="telephone" className='hero__img-mobile'/>
                <div className="hero__content">
                    <p className="hero__subtitle">{t('hero.subtitle')}</p>
                    <h1 className="hero__title gradient">{t('hero.title')}</h1>
                    <p className="hero__descr">{t('hero.descr')}</p>
                    <button className="hero__btn">Download</button>
                </div>
            </div>
        </div>
      </section>
   );
}